import React, { useState, useEffect, useRef } from 'react'
import PropTypes from 'prop-types'
import styles from './TableBody.module.css'
import { propTypes } from 'react-bootstrap/esm/Image'

function TableBody({
  data,
  headers,
  onDoubleClickSelectData,
  onAction,
  onMultiSelect,
  clearSelected,
  setClearSelected,
  shouldScrollToView,
  setShouldScrollToView,
  onSingleClickSelectData,
  defaultSelected,
  selectEnabled,
}) {
  const orderRef = useRef()
  const [selectedData, setSelectedData] = useState()
  const [selectedDatas, setSelectedDatas] = useState([])

  const selectItem = (item) => {
    setSelectedData(item[Object.keys(item)[0]])
    if (typeof onSingleClickSelectData === 'function')
      onSingleClickSelectData(item)
  }

  const selectMultipleItems = (item) => {
    if (
      selectedDatas.length > 0 &&
      selectedDatas.filter(
        (selected) =>
          selected[Object.keys(selected)[0]] === item[Object.keys(item)[0]]
      ).length > 0
    ) {
      const remainingDatas = selectedDatas.filter(
        (obj) => obj[Object.keys(obj)[0]] !== item[Object.keys(item)[0]]
      )

      setSelectedDatas(remainingDatas)
    } else {
      setSelectedDatas([...selectedDatas, item])
    }
  }

  const onClickExecuteOnAction = (e, actionName, id, disabled, rowDetails) => {
    e.stopPropagation()
    if (!disabled && typeof onAction === 'function') onAction(actionName, id, rowDetails)
  }

  const bodyColumn = (property, index, rowDetails) => {
    if (
      (property && property.hidden)
      || Array.isArray(property)
      || (property && String(property).includes('hidden:'))
    )
      return

    if (typeof property === 'object' && property !== null) {
      return (
        <td
          key={`${property.id}${index}`}
          title={property.toolTip}
          role='presentation'
          style={{
            width:
              (property.icon &&
                !property.text &&
                headers[headers.length - 1] &&
                headers[headers.length - 1].headerName === 'â€Ž' &&
                '50px') ||
              (property.icon &&
                !property.text &&
                headers[headers.length - 1] &&
                '120px'),
          }}
        >
          {property.icon && (
            <img
              style={{ marginRight: '10px' }}
              src={property.icon} alt=''
              onClick={(e) =>
                onClickExecuteOnAction(
                  e,
                  property.actionName,
                  property.id,
                  property.disabled,
                  rowDetails,
                )
              }
              onKeyDown={(e) =>
                onClickExecuteOnAction(
                  e,
                  property.actionName,
                  property.id,
                  property.disabled,
                  rowDetails,
                )
              }
            />
          )}
          <span style={{ marginTop: '8px' }}>{property.text}</span>
        </td>
      )
    }
    return (
      <td key={index} title={property && property}>
        {property}
      </td>
    )
  }

  const scrollToView = () => {
    setTimeout(() => {
      orderRef.current.scrollIntoView({
        behavior: 'smooth',
        block: 'center',
      })
    }, 500)
  }

  useEffect(() => {
    if (typeof onMultiSelect === 'function') onMultiSelect(selectedDatas)

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [selectedDatas])

  useEffect(() => {
    if (shouldScrollToView && orderRef) {
      scrollToView()
      setShouldScrollToView(false)
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [shouldScrollToView])

  useEffect(() => {
    if (selectedDatas.length > 0) onMultiSelect(selectedDatas)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [selectedDatas])

  useEffect(() => {
    if (clearSelected) {
      setClearSelected(false)
      setSelectedDatas([])
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [clearSelected])

  useEffect(() => {
    defaultSelected && setSelectedData(defaultSelected.id)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [defaultSelected])

  const body = () => {
    if (data !== null) {
      return data.map((dataObject) => (
        <tr
          ref={dataObject.scrollTo ? orderRef : null}
          key={dataObject[Object.keys(dataObject)[0]]}
          className={`${
            selectedData === dataObject[Object.keys(dataObject)[0]] ||
            selectedDatas.filter(
              (item) =>
                item[Object.keys(item)[0]] ===
                dataObject[Object.keys(dataObject)[0]]
            ).length > 0
              ? styles.selectedRow
              : styles.row
          }
            ${dataObject.isHighlighted && styles.isHighlighted}
          `}
          onClick={() => {

            if (selectEnabled) {
              typeof onMultiSelect === 'function'
                ? selectMultipleItems(dataObject)
                : selectItem(dataObject)
            }
          }}
          onDoubleClick={() =>
            typeof onDoubleClickSelectData === 'function' &&
            onDoubleClickSelectData(dataObject)
          }
        >
          {Object.entries(dataObject).map(
            ([keyWord, property], index) =>
              index > 0 &&
              keyWord !== 'scrollTo' &&
              keyWord !== 'isHighlighted' &&
              bodyColumn(property, index, dataObject)
          )}
        </tr>
      ))
    }
    return null
  }

  return body()
}
TableBody.propTypes = {
  data: PropTypes.arrayOf(PropTypes.shape),
  onDoubleClickSelectData: PropTypes.func,
  onAction: PropTypes.func,
  onMultiSelect: PropTypes.func,
  clearSelected: PropTypes.bool,
  setClearSelected: PropTypes.func,
  shouldScrollToView: PropTypes.bool,
  setShouldScrollToView: PropTypes.func,
  defaultSelected: PropTypes.shape(PropTypes.any),
  headers: PropTypes.shape(PropTypes.any),
}
TableBody.defaultProps = {
  data: null,
  headers: null,
  onDoubleClickSelectData: null,
  onAction: null,
  onMultiSelect: null,
  clearSelected: false,
  setClearSelected: null,
  shouldScrollToView: false,
  setShouldScrollToView: null,
  defaultSelected: null,
}
export default TableBody
