import React, { useRef } from 'react';
import PropTypes from 'prop-types';

import styles from './table.module.css';

import TableBody from './table-body/TableBody';
import TableFooter from './table-footer/TableFooter';
import TableHeader from './table-header/TableHeader';
import BootstrapSpinner from '../bootstrap-spinner/BootstrapSpinner';

function Table({
  headers,
  list,
  noDataVerbiage,
  paginationParameters,
  sortParameters,
  queryParameters,
  isFetchingData,
  isExporting,
  onSort,
  onFilter,
  onClearFilters,
  onPaginate,
  onExport,
  onDoubleClickSelectData,
  onAction,
  onMultiSelect,
  clearSelected,
  setClearSelected,
  shouldScrollToView,
  setShouldScrollToView,
  onSetFooterHeight,
  tableHeight,
  onSingleClickSelectData,
  defaultSelected,
  selectEnabled,
  rowStyle,
  boxShadowStyle,
}) {
  const tableBodyWrapperRef = useRef();

  const setFooterHeight = (height) => {
    if (typeof onSetFooterHeight === 'function') {
      onSetFooterHeight(height);
    }
  };

  const tableBody = () => {
    if (list && list.length > 0) {
      return (
        <tbody className={styles.tbody}>
          <TableBody
            data={list}
            headers={headers}
            onDoubleClickSelectData={onDoubleClickSelectData}
            onAction={onAction}
            onMultiSelect={onMultiSelect}
            clearSelected={clearSelected}
            setClearSelected={setClearSelected}
            shouldScrollToView={shouldScrollToView}
            setShouldScrollToView={setShouldScrollToView}
            onSingleClickSelectData={onSingleClickSelectData}
            defaultSelected={defaultSelected}
            selectEnabled={selectEnabled}
            rowStyle={rowStyle}
          />
        </tbody>
      );
    }
    return (
      <div className={styles.tbodyDiv}>
        <p className={styles.noDataVerbiage}>{noDataVerbiage}</p>
      </div>
    );
  };

  return (
    <div className={`${styles.boxShadow} ${boxShadowStyle}`}>
      <table className={styles.table}>
        <thead className={styles.thead}>
          <TableHeader
            data={list}
            isFetchingData={isFetchingData}
            sortParameters={sortParameters}
            queryParameters={queryParameters}
            headers={headers}
            onSort={onSort}
            onFilter={onFilter}
            onClearFilters={onClearFilters}
          />
        </thead>
        <div
          ref={tableBodyWrapperRef}
          className={`${styles.tableBodyWrapper} ${rowStyle}`}
          style={{ height: `calc(100vh - ${tableHeight})` }}
        >
          {isFetchingData ? (
            <div className={styles.tbodyDiv}>
              <BootstrapSpinner />
            </div>
          ) : (
            tableBody()
          )}
        </div>
        <TableFooter
          isExporting={isExporting}
          isFetchingData={isFetchingData}
          paginationParameters={paginationParameters}
          onPaginate={onPaginate}
          onExport={onExport}
          paginationData={paginationParameters}
          footerHeightChange={setFooterHeight}
          rowStyle={rowStyle}
        />
      </table>
    </div>
  );
}

Table.propTypes = {
  list: PropTypes.arrayOf(PropTypes.object),
  noDataVerbiage: PropTypes.string,
  headers: PropTypes.arrayOf(
    PropTypes.shape({
      headerId: PropTypes.number.isRequired,
      headerName: PropTypes.string.isRequired,
      filter: PropTypes.shape({
        type: PropTypes.number,
        queryNames: PropTypes.arrayOf(PropTypes.string),
        list: PropTypes.arrayOf(
          PropTypes.shape({
            id: PropTypes.number,
            name: PropTypes.string,
          })
        ),
      }),
    })
  ).isRequired,
  paginationParameters: PropTypes.exact({
    currentPage: PropTypes.number,
    pageSize: PropTypes.number,
    totalCount: PropTypes.number,
    totalPages: PropTypes.number,
  }),
  sortParameters: PropTypes.exact({
    order: PropTypes.number,
    sort: PropTypes.number,
  }),
  isFetchingData: PropTypes.bool,
  isExporting: PropTypes.bool,
  onClearFilters: PropTypes.func,
  onDoubleClickSelectData: PropTypes.func,
  onSort: PropTypes.func,
  onPaginate: PropTypes.func,
  onExport: PropTypes.func,
  queryParameters: PropTypes.arrayOf(PropTypes.shape),
  onFilter: PropTypes.func,
  onAction: PropTypes.func,
  onMultiSelect: PropTypes.func,
  onSetFooterHeight: PropTypes.func,
  clearSelected: PropTypes.bool,
  setClearSelected: PropTypes.func,
  shouldScrollToView: PropTypes.bool,
  setShouldScrollToView: PropTypes.func,
  tableHeight: PropTypes.func,
  onSingleClickSelectData: PropTypes.func,
  defaultSelected: PropTypes.shape,
  selectEnabled: PropTypes.bool,
};
Table.defaultProps = {
  noDataVerbiage: 'No Data Found',
  list: [],
  paginationParameters: null,
  sortParameters: null,
  isFetchingData: true,
  isExporting: false,
  onClearFilters: null,
  onDoubleClickSelectData: null,
  onSort: null,
  onPaginate: null,
  onExport: null,
  queryParameters: null,
  onFilter: null,
  onAction: null,
  onMultiSelect: null,
  clearSelected: false,
  setClearSelected: null,
  shouldScrollToView: false,
  setShouldScrollToView: null,
  onSetFooterHeight: null,
  tableHeight: '250px',
  onSingleClickSelectData: null,
  defaultSelected: null,
  selectEnabled: true,
};
export default Table;
