import React, {
  forwardRef,
  useImperativeHandle,
  useState,
  useRef,
  useEffect,
} from 'react';
import PropTypes from 'prop-types';
import styles from './styles.module.css';

import NumberRangeEnums from './NumberRange.enums';
import Button from '../../button/Button';

const NumberRange = forwardRef(({ queryNames, onFilter, onHideForm }, ref) => {
  const [isSelectionActive, setIsSelectionActive] = useState(false);
  const [isFilterDisabled, setIsFilterDisabled] = useState(false);
  const [selectedOption, setSelectedOption] = useState(
    NumberRangeEnums.options[0],
  );
  const [value, setValue] = useState('');
  const optionRef = useRef();

  const backdropClicked = (e) => {
    if (optionRef.current && !optionRef.current.contains(e.target)) {
      setIsSelectionActive(false);
    }
  };

  const resetForm = () => {
    setValue('');
    setSelectedOption(NumberRangeEnums.options[0]);
    setIsFilterDisabled(true);
  };

  const handleSetIsSelectionActive = () => {
    setIsSelectionActive(!isSelectionActive);
  };

  const onChangedSetValue = (e) => {
    setValue(e.target.value);
    setIsFilterDisabled(false);
  };

  const onChangedSetSelectedOption = (option) => {
    setSelectedOption(option);
    setIsFilterDisabled(false);
  };

  const executeFilter = () => {
    onFilter(
      { [queryNames[0]]: selectedOption.optionId, [queryNames[1]]: value },
      queryNames,
    );
    onHideForm('');
    setIsFilterDisabled(true);
  };

  const executeClearFilter = () => {
    onFilter(
      { [queryNames[0]]: undefined, [queryNames[1]]: undefined },
      queryNames,
    );
    onHideForm('');
    resetForm();
  };

  useImperativeHandle(ref, () => ({
    clearFilters() {
      setSelectedOption(NumberRangeEnums.options[0]);
      setIsFilterDisabled(true);
      setValue('');
    },
  }));

  useEffect(() => {
    document.addEventListener('mousedown', backdropClicked);
    return () => document.removeEventListener('mousedown', backdropClicked);
  });

  return (
    <div className={styles.body}>
      <div />
      <button
        className={styles.select}
        onClick={handleSetIsSelectionActive}
        type="button"
      >
        {selectedOption.optionName}

        {isSelectionActive && (
          <div className={styles.options} ref={optionRef}>
            {NumberRangeEnums.options.map((option) => (
              <div
                key={option.id}
                className={styles.optionItem}
                onClick={() => onChangedSetSelectedOption(option)}
                onKeyDown={() => onChangedSetSelectedOption(option)}
                role="presentation"
              >
                {option.optionName}
              </div>
            ))}
          </div>
        )}
      </button>

      <input
        className={styles.input}
        type="number"
        value={value}
        placeholder="Enter the value"
        onChange={onChangedSetValue}
      />

      <hr style={{ backgroundColor: '#6d6d73' }} />

      <Button text="Clear" onClick={executeClearFilter} defaultBtn />
      <Button
        defaultBtn={false}
        text="Filter"
        onClick={executeFilter}
        disabled={value.length === 0 || isFilterDisabled}
      />
    </div>
  );
});
NumberRange.propTypes = {
  queryNames: PropTypes.arrayOf(PropTypes.string).isRequired,
  onFilter: PropTypes.func.isRequired,
  onHideForm: PropTypes.func.isRequired,
};
NumberRange.displayName = 'NumberRange';
export default NumberRange;
