import React, { useState, useRef, useEffect } from 'react';
import PropTypes from 'prop-types';
import Styles from './TableFooter.module.css';
import chevLeft from '../../../../assets/icons/Chev-Left.png';
import chevRight from '../../../../assets/icons/Chev-Right.png';
import chevLeftFirst from '../../../../assets/icons/Chev-Left-First.png';
import chevRightLast from '../../../../assets/icons/Chev-Right-Last.png';

function TableFooterComponent(props) {
  const {
    isExporting,
    isFetchingData,
    paginationParameters,
    onExport,
    onPaginate,
    paginationData,
    footerHeightChange,
    rowStyle,
  } = props;
  const [showPageSizeOptions, setShowPageSizeOptions] = useState(false);
  const pageSizeOptions = [50, 100, 200];
  const totalPages = Math.trunc(paginationParameters.totalPages);
  const firstPages = [1, 2, 3];
  const initialButtons = [1, 2, 3, 4, 5];
  const [middlePages, setMiddlePages] = useState([4, 5, 6, 7, 8]);
  const lastPages = [totalPages - 2, totalPages - 1, totalPages];
  const pageSizeOptionsRef = useRef();
  const footerRef = useRef();

  const paginateTo = (currentPage) => {
    if (currentPage < totalPages - 3 || currentPage > totalPages - 3) {
      setMiddlePages([
        (middlePages[0] = totalPages - 6),
        (middlePages[1] = totalPages - 5),
        (middlePages[2] = totalPages - 4),
        (middlePages[3] = totalPages - 3),
        (middlePages[4] = totalPages - 2),
      ]);
    }
    if (currentPage > 6 && currentPage < totalPages - 4) {
      setMiddlePages([
        (middlePages[0] = currentPage - 2),
        (middlePages[1] = currentPage - 1),
        (middlePages[2] = currentPage),
        (middlePages[3] = currentPage + 1),
        (middlePages[4] = currentPage + 2),
      ]);
    }
    if (currentPage <= 6) {
      setMiddlePages([
        (middlePages[0] = 3),
        (middlePages[1] = 4),
        (middlePages[2] = 5),
        (middlePages[3] = 6),
        (middlePages[4] = 7),
      ]);
    }
  };

  const paginateToFirstPage = () => {
    onPaginate(1, paginationData.pageSize);
    paginateTo(1);
  };

  const paginateToLastPage = () => {
    onPaginate(totalPages, paginationData.pageSize);
    paginateTo(totalPages);
  };

  const decrementPageButtonNumbers = () => {
    if (
      paginationData.currentPage > 6 &&
      paginationData.currentPage < totalPages - 4
    ) {
      setMiddlePages([
        middlePages[0] - 1,
        middlePages[1] - 1,
        middlePages[2] - 1,
        middlePages[3] - 1,
        middlePages[4] - 1,
      ]);
    }
  };

  const incrementPageButtonNumbers = () => {
    if (
      paginationData.currentPage > 5 &&
      paginationData.currentPage < totalPages - 5
    ) {
      setMiddlePages([
        middlePages[0] + 1,
        middlePages[1] + 1,
        middlePages[2] + 1,
        middlePages[3] + 1,
        middlePages[4] + 1,
      ]);
    }
  };

  const togglePageSizeOptions = () => {
    setShowPageSizeOptions(!showPageSizeOptions);
  };

  const handlePreviousPage = () => {
    if (paginationData.currentPage > 1) {
      onPaginate(paginationData.currentPage - 1, paginationData.pageSize);
    }
    decrementPageButtonNumbers();
  };

  const setCurrentPage = (page) => {
    onPaginate(page, paginationData.pageSize);
    paginateTo(page);
  };

  const handleNextPage = () => {
    if (paginationData.currentPage < paginationData.totalPages) {
      onPaginate(paginationData.currentPage + 1, paginationData.pageSize);
    }
    incrementPageButtonNumbers();
  };

  const handlePageSize = (size) => {
    onPaginate(1, size);
    setShowPageSizeOptions(false);
  };

  useEffect(() => {
    if (footerRef.current) {
      footerHeightChange(footerRef.current.clientHeight);
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [footerRef.current && footerRef.current.clientHeight]);

  const pageFrom = () => {
    if (paginationParameters.totalCount > 0) {
      if (paginationParameters.currentPage > 1) {
        return (
          (paginationParameters.currentPage - 1) *
            paginationParameters.pageSize +
          1
        );
      }
      return 1;
    }
    return 0;
  };

  const pageTo = () => {
    if (paginationParameters.totalCount > 0) {
      if (
        paginationParameters.currentPage > 1 &&
        (paginationParameters.currentPage - 1) * paginationParameters.pageSize +
          paginationParameters.pageSize <
          paginationParameters.totalCount
      ) {
        const computeTo =
          (paginationParameters.currentPage - 1) *
            paginationParameters.pageSize +
          paginationParameters.pageSize;
        return computeTo;
      }
      if (paginationParameters.pageSize) {
        if (paginationParameters.totalCount > paginationParameters.pageSize) {
          if (paginationParameters.currentPage === totalPages) {
            return paginationParameters.totalCount;
          }
          return paginationParameters.pageSize;
        }
        return paginationParameters.totalCount;
      }
      return 0;
    }
    return 0;
  };

  const paginationBtn = () => {
    if (paginationParameters.totalCount > 0) {
      if (paginationParameters.pageSize) {
        if (paginationParameters.totalCount > paginationParameters.pageSize) {
          return paginationParameters.pageSize;
        }
        return paginationParameters.totalCount;
      }
      return 0;
    }
    return 0;
  };

  const moreThanFive = () => {
    if (
      paginationParameters.currentPage > 2 &&
      paginationParameters.currentPage < totalPages - 1
    ) {
      return (
        <>
          {/* Render if total pages is more than 5 and you're in page 6 above */}
          <button
            className={`${Styles.paginationBtn} ${
              paginationParameters.currentPage === 1
                ? Styles.paginationActiveBtn
                : null
            }`}
            type="button"
            onClick={() => setCurrentPage(1)}
          >
            1
          </button>
          <button className={Styles.paginationBtn} type="button">
            ..
          </button>
          {middlePages.map((button) => (
            <button
              className={`${Styles.paginationBtn} ${
                button === paginationParameters.currentPage
                  ? Styles.paginationActiveBtn
                  : null
              }`}
              type="button"
              key={button.id}
              onClick={() => setCurrentPage(button)}
            >
              {button}
            </button>
          ))}
          <button className={Styles.paginationBtn} type="button">
            ..
          </button>
          <button
            className={`${Styles.paginationBtn} ${
              Math.trunc(paginationParameters.totalPages) ===
              paginationParameters.currentPage
                ? Styles.paginationActiveBtn
                : null
            }`}
            type="button"
            onClick={() =>
              setCurrentPage(Math.trunc(paginationParameters.totalPages))
            }
          >
            {Math.trunc(paginationParameters.totalPages)}
          </button>
        </>
      );
    }
    return (
      <>
        {/* Render if total pages is more than 5 and you're in
            page 1/2/3/lastpage - 2/lastpage - 1/lastpage */}
        {firstPages.map((button) => (
          <button
            className={`${Styles.paginationBtn} ${
              button === paginationParameters.currentPage
                ? Styles.paginationActiveBtn
                : null
            }`}
            type="button"
            key={button.id}
            onClick={() => setCurrentPage(button)}
          >
            {button}
          </button>
        ))}
        <button className={Styles.paginationBtn} type="button">
          ..
        </button>
        {lastPages.map((button) => (
          <button
            className={`${Styles.paginationBtn} ${
              button === paginationParameters.currentPage
                ? Styles.paginationActiveBtn
                : null
            }`}
            type="button"
            key={button.id}
            onClick={() => setCurrentPage(button)}
          >
            {button}
          </button>
        ))}
      </>
    );
  };

  return (
    <div ref={footerRef} className={`${Styles.footer} ${rowStyle}`}>
      <div>
        <div
          className="d-flex"
          hidden={paginationParameters.totalPages === 0 || isFetchingData}
        >
          <div className={`${Styles.labelCol} d-flex align-items-center`}>
          <span className={Styles.label}>Listing</span>
          <span className="position-relative">
            <button
              className={Styles.listSelection}
              onClick={togglePageSizeOptions}
              disabled={paginationParameters.totalCount === 0}
              type="button"
            >
              {paginationBtn()}
            </button>
            <div
              ref={pageSizeOptionsRef}
              className={Styles.pageSizeOptions}
              style={{
                display: showPageSizeOptions ? null : 'none',
                height: `${pageSizeOptions.length * 25}px`,
                marginTop: `-${pageSizeOptions.length * 25 - 1}px`,
              }}
            >
              {pageSizeOptions.map((value) => (
                <div
                  className={Styles.optionItem}
                  key={value.id}
                  onClick={() => handlePageSize(value)}
                  onKeyDown={() => handlePageSize(value)}
                  role="presentation"
                >
                  {value}
                </div>
              ))}
            </div>
          </span>
          <div className={Styles.labelOverflowHidden}>
            <span className={Styles.label}>
              entries, showing {pageFrom()} to {pageTo()} of{' '}
              {paginationParameters.totalCount
                ? paginationParameters.totalCount
                : 0}
            </span>
          </div>
          {typeof onExport === 'function' && (
            <button
              className={Styles.exportButton}
              onClick={onExport}
              style={{
                left: paginationParameters.totalPages < 2 ? null : '0',
                marginRight:
                  paginationParameters.totalPages < 2 ? '20px' : 'auto',
                marginLeft: paginationParameters.totalPages < 2 ? null : 'auto',
              }}
              disabled={
                paginationParameters.totalCount === 0
                  ? true
                  : false || isExporting
              }
              type="button"
            >
              Export{' '}
              {isExporting && (
                <i className="fas fa-circle-notch fa-spin mx-2" />
              )}
            </button>
          )}
          </div>
          <div className={Styles.paginationCol}>
          <div
            className="btn-group float-end"
            role="group"
            style={{ display: totalPages > 1 ? null : 'none' }}
          >
            <button
              className={Styles.paginationBtn}
              type="button"
              onClick={paginateToFirstPage}
            >
              <img
                className={Styles.chevFirstPage}
                src={chevLeftFirst}
                alt="Paginate-Left-First"
              />
            </button>
            <button
              className={Styles.paginationBtn}
              type="button"
              onClick={handlePreviousPage}
            >
              <img
                className={Styles.chevLeftIcon}
                src={chevLeft}
                alt="Paginate-Left"
              />
            </button>
            {paginationParameters.totalPages > 5 ? (
              moreThanFive()
            ) : (
              <>
                {totalPages === 5 && (
                  <>
                    {/* Render if total pages is Equal to 5 */}
                    {initialButtons.map((button) => (
                      <button
                        className={`${Styles.paginationBtn} ${
                          button === paginationParameters.currentPage
                            ? Styles.paginationActiveBtn
                            : null
                        }`}
                        type="button"
                        key={button.id}
                        onClick={() => setCurrentPage(button)}
                      >
                        {button}
                      </button>
                    ))}
                  </>
                )}
                {totalPages !== 5 && (
                  <>
                    {/* Render if total pages is less than 5 */}
                    {initialButtons
                      .slice(0, paginationParameters.totalPages)
                      .map((button) => (
                        <button
                          className={`${Styles.paginationBtn} ${
                            button === paginationParameters.currentPage
                              ? Styles.paginationActiveBtn
                              : null
                          }`}
                          type="button"
                          key={button.id}
                          onClick={() => setCurrentPage(button)}
                        >
                          {button}
                        </button>
                      ))}
                  </>
                )}
              </>
            )}
            <button
              className={Styles.paginationBtn}
              type="button"
              onClick={handleNextPage}
            >
              <img
                className={Styles.chevRightIcon}
                src={chevRight}
                alt="Paginate-Right"
              />
            </button>
            <button
              className={Styles.paginationBtn}
              type="button"
              onClick={paginateToLastPage}
            >
              <img
                className={Styles.chevLastPage}
                src={chevRightLast}
                alt="Paginate-Right-Last"
              />
            </button>
          </div>
       
      </div>
          </div>

          
      </div>
    </div>
  );
}

TableFooterComponent.propTypes = {
  paginationParameters: PropTypes.shape.isRequired,
  isExporting: PropTypes.bool,
  onExport: PropTypes.func.isRequired,
  isFetchingData: PropTypes.bool,
  onPaginate: PropTypes.func.isRequired,
  paginationData: PropTypes.shape.isRequired,
  footerHeightChange: PropTypes.number,
};
TableFooterComponent.defaultProps = {
  isExporting: false,
  isFetchingData: true,
  footerHeightChange: 63,
};
export default TableFooterComponent;
