import React, { forwardRef, useImperativeHandle, useState } from 'react';
import PropTypes from 'prop-types';
import Calendar from 'react-calendar';

import './react-calendar.css';
import styles from './styles.module.css';

import DateEnums from './Date.enums';
import Button from '../../button/Button';

const DateComponent = forwardRef(
  ({
    queryNames, onFilter, onHideForm, index,
  }, ref) => {
    const convertDateToString = (date) => `${
      DateEnums.monthNames[date.getMonth()]
    } ${date.getDate()}, ${date.getFullYear()}`;

    const dateTodayString = convertDateToString(new Date());
    const [startDateString, setStartDateString] = useState(dateTodayString);
    const [endDateString, setEndDateString] = useState(dateTodayString);
    const [totalDays, setTotalDays] = useState(1);

    const [selectedPeriod, setSelectedPeriod] = useState(4);
    const [startDate, setStartDate] = useState(new Date());
    const [endDate, setEndDate] = useState(new Date());
    const [startDateData, setCustomStartDate] = useState();
    const [endDateData, setCustomEndDate] = useState();

    const calculateTotalDaysRange = (start, end) => {
      const miliSecondsPerDay = 86400000;
      const timeDifference = end.getTime() - start.getTime();
      const totalDaysComputed = Math.round(timeDifference / miliSecondsPerDay);
      setTotalDays(totalDaysComputed + 1);
    };

    const selectPeriod = (id) => {
      setSelectedPeriod(id);
      setCustomStartDate();
      setCustomEndDate();

      switch (id) {
        case 1:
          setStartDate(new Date());
          calculateTotalDaysRange(new Date(), new Date());
          setStartDateString(convertDateToString(new Date()));
          break;
        case 2: {
          const twoDaysAgo = new Date();
          twoDaysAgo.setDate(twoDaysAgo.getDate() - 1);
          setStartDate(twoDaysAgo);
          calculateTotalDaysRange(twoDaysAgo, new Date());
          setStartDateString(convertDateToString(twoDaysAgo));
          break;
        }
        case 3: {
          const threeDaysAgo = new Date();
          threeDaysAgo.setDate(threeDaysAgo.getDate() - 2);
          setStartDate(threeDaysAgo);
          calculateTotalDaysRange(threeDaysAgo, new Date());
          setStartDateString(convertDateToString(threeDaysAgo));
          break;
        }
        case 4:
          setStartDate(new Date());
          setCustomEndDate(new Date().setDate(new Date().getDate() - 1));
          calculateTotalDaysRange(new Date(), new Date());
          setStartDateString(convertDateToString(new Date()));
          break;
        default:
          break;
      }

      setEndDate(new Date());
      setEndDateString(convertDateToString(new Date()));
    };

    const onClickSetStartDate = (day) => {
      setStartDate(day);
      setCustomStartDate(day);
      setStartDateString(convertDateToString(day));
      calculateTotalDaysRange(day, endDate);
    };

    const onClickSetEndDate = (day) => {
      setEndDate(day);
      setCustomEndDate(day);
      setEndDateString(convertDateToString(day));
      calculateTotalDaysRange(startDate, day);
    };

    const onClickFilter = () => {
      var customStartDate = startDateData && new Date(startDateData)
      var customEndDate = endDateData && new Date(endDateData)
      if (customStartDate) customStartDate.setDate(customStartDate.getDate() + 1)
      if (customEndDate) customEndDate.setDate(customEndDate.getDate() + 1)

      var start = customStartDate ? customStartDate : startDate
      var end = customEndDate ? customEndDate : endDate

      onFilter(
        {
          [queryNames[0]]: selectedPeriod,
          [queryNames[1]]: start,
          [queryNames[2]]: end,
        },
        queryNames
      )
      onHideForm()
    };

    const setFormToDefault = () => {
      setSelectedPeriod(4);
      setStartDate(new Date());
      setEndDate(new Date());
      setStartDateString(dateTodayString);
      setEndDateString(dateTodayString);
      setCustomStartDate();
      setCustomEndDate();
      setTotalDays(1);
    };

    const onClickClearFilter = () => {
      onFilter(
        {
          [queryNames[0]]: undefined,
          [queryNames[1]]: undefined,
          [queryNames[2]]: undefined,
        },
        queryNames,
      );
      onHideForm();
      setFormToDefault();
    };

    useImperativeHandle(ref, () => ({
      clearFilters() {
        setFormToDefault();
      },
    }));

    return (
      <div
        className={styles.body}
        style={{
          left: index < 2 && '0',
          right: index > 1 && '0',
          marginLeft: index === 0 && '25px',
        }}
      >
        <div className={styles.list}>
          {DateEnums.periods.map((item) => (
            <div
              key={item.id}
              className={`${styles.item} ${
                selectedPeriod === item.id ? styles.activeItem : null
              }`}
              onClick={() => selectPeriod(item.id)}
              onKeyDown={() => selectPeriod(item.id)}
              role="presentation"
            >
              {item.name}
            </div>
          ))}
        </div>

        <div className={styles.dateSection}>
          <div className={styles.dateRange}>
            <p className={styles.dateRangeText}>{startDateString}</p>
            <span className={styles.arrowRightLongIcon} />
            <p className={styles.dateRangeText}>{endDateString}</p>
          </div>

          <span className={styles.totalDaysText}>
            {totalDays}
            {' '}
            Day(s)
          </span>

          <div style={{ display: 'flex', marginTop: '5px' }}>
            <div
              className={styles.startSection}
              style={{ pointerEvents: selectedPeriod !== 4 ? 'none' : null }}
            >
              <Calendar
                tileClassName={({ date }) => (date > startDate && date <= endDate
                  ? 'react-calendar__month-view__days__day_between-day-range'
                  : null)}
                onClickDay={onClickSetStartDate}
                value={startDate}
              />
            </div>
            <div className={styles.divider} />
            <div
              className={styles.endSection}
              style={{ pointerEvents: selectedPeriod !== 4 ? 'none' : null }}
            >
              <Calendar
                tileClassName={({ date }) => (date >= startDate && date < endDate
                  ? 'react-calendar__month-view__days__day_between-day-range'
                  : null)}
                minDate={startDate}
                onClickDay={onClickSetEndDate}
                value={endDate}
              />
            </div>
          </div>

          <div className={styles.footer}>
            <Button text="Clear" defaultBtn onClick={onClickClearFilter} />
            <Button defaultBtn={false} text="Filter" onClick={onClickFilter} />
          </div>
        </div>
      </div>
    );
  },
);
DateComponent.propTypes = {
  queryNames: PropTypes.arrayOf(PropTypes.string).isRequired,
  onFilter: PropTypes.func.isRequired,
  onHideForm: PropTypes.func.isRequired,
  index: PropTypes.number,
};
DateComponent.defaultProps = {
  index: 0,
};
DateComponent.displayName = 'DateComponent';
export default DateComponent;
