import React, {
  useEffect, useRef, useState, createRef, useMemo,
} from 'react';
import PropTypes from 'prop-types';
import styles from './TableHeader.module.css';
import filterIcon from '../../../assets/icons/filter.svg';
import filteredIcon from '../../../assets/icons/filtered.svg';
import arrowUpIcon from '../../../assets/icons/arrow-up.svg';
import clearFilterIcon from '../../../assets/icons/clear-filter.svg';
import Filter from './Filter';

function TableHeader({
  data,
  isFetchingData,
  sortParameters,
  queryParameters,
  headers,
  onSort,
  onFilter,
  onClearFilters,
}) {
  const filterRef = useRef();
  const childRefs = useMemo(() => headers.map(() => createRef()), [headers]);
  const [activeDropdown, setActiveDropdown] = useState('');
  const canSort = typeof onSort === 'function';
  const [tableHeaders, setTableHeaders] = useState();
  const [isLastColumnSmall, setIsLastColumnSmall] = useState(false);
  const [isLastColumnSmallWithText, setIsLastColumnSmallWithText] = useState(false);

  const backdropClicked = (e) => {
    if (filterRef.current && !filterRef.current.contains(e.target)) {
      setActiveDropdown('');
    }
  };

  const handleOnFilter = (list) => {
    if (typeof onFilter === 'function') onFilter(list);
  };

  const handleOnClearFilters = () => {
    if (typeof onClearFilters === 'function') onClearFilters();
    for (let i = 0; i < childRefs.length; i += 1) {
      childRefs[i].current.clearFilters();
    }
  };

  const executeSortData = (headerId) => {
    let sortOrder = 1;
    if (sortParameters.sort === headerId && sortParameters.order !== 1) sortOrder = 1;
    else sortOrder = 2;

    if (canSort) {
      onSort({
        sortOrder,
        sortBy: headerId,
      });
    }
  };

  const onClickSetActiveDropdown = (headerName) => {
    setActiveDropdown(headerName);
  };

  const checkIfColumnIsAnIconOnly = (items) => {
    if (
      (typeof items[0] === 'object'
      && Object.prototype.hasOwnProperty.call(items[0], 'action')
      && items[0].action.icon
      && !items[0].action.text)
      && headers[headers.length - 1]
      && headers[headers.length - 1].headerName === 'â€Ž'
    ) {
      return true;
    }

    return false;
  };

  const checkIfColumnIsAnIconOnlyAndHeaderHasText = (items) => {
    if (
      (typeof items[0] === 'object'
      && Object.prototype.hasOwnProperty.call(items[0], 'action')
      && items[0].action.icon
      && !items[0].action.text)
      && headers[headers.length - 1]
    ) {
      return true;
    }

    return false;
  };

  useEffect(() => {
    document.addEventListener('mousedown', backdropClicked);
    return () => document.removeEventListener('mousedown', backdropClicked);
  });

  useEffect(() => {
    if (headers) setTableHeaders(headers);
  }, [headers]);

  useEffect(() => {
    if (data.length > 0) {
      setIsLastColumnSmall(checkIfColumnIsAnIconOnly(data));
      setIsLastColumnSmallWithText(checkIfColumnIsAnIconOnlyAndHeaderHasText(data));
    }
  }, [data]);

  const header = (column, index) => {
    if (!column.hidden) {
      return (
        <th
          key={column.headerId}
          className={styles.columns}
          style={{
            width: (headers.length - 1 === index
              && isLastColumnSmall
              && '65px')
              || (headers.length - 1 === index
              && isLastColumnSmallWithText
              && '120px'),
          }}
        >
          <div style={{ display: 'flex', paddingRight: '20px' }}>
            <div
              className={styles.label}
              style={{
                cursor: canSort && column.sortable && 'pointer',
                
              }}
              title={column.headerName}
              onClick={() => canSort && column.sortable && executeSortData(column.headerId)}
              onKeyDown={() => canSort && column.sortable && executeSortData(column.headerId)}
              role="presentation"
            >
              {column.headerName}
            </div>

            {column.filter && (
              <div className={styles.icon}>
                <img
                  src={
                    column.filter.queryNames.every(
                      (name) => queryParameters[name],
                    )
                      ? filteredIcon
                      : filterIcon
                  }
                  alt="FilterIcon"
                  className={styles.filterIcons}
                  onClick={() => onClickSetActiveDropdown(column.headerName)}
                  onKeyDown={() => onClickSetActiveDropdown(column.headerName)}
                  role="presentation"
                />
              </div>
            )}

            {sortParameters && (
              <div className={styles.icon}>
                <img
                  className={styles.arrowIcons}
                  src={arrowUpIcon}
                  alt="ArrowUpIcon"
                  style={{
                    marginLeft: !column.filter && '4px',
                    display: column.headerId !== sortParameters.sort && 'none',
                    transform:
                      sortParameters.order === 1
                        ? 'rotate(0deg)'
                        : 'rotate(180deg)',
                  }}
                />
              </div>
            )}

            {queryParameters
              && !Object.values(queryParameters).every(
                (prop) => prop === undefined,
              ) && (
                <div className={styles.icon}>
                  <img
                    className={styles.clearAllFiltersIcon}
                    style={{
                      marginTop: '-2px',
                      marginLeft:
                        column.headerName === 'â€Ž'
                        && headers.length - 1 === index
                        && isLastColumnSmall
                        && '-1em',
                    }}
                    src={clearFilterIcon}
                    alt="ClearFilterIcon"
                    title="Clear all filters"
                    onClick={handleOnClearFilters}
                    onKeyDown={handleOnClearFilters}
                    role="presentation"
                    hidden={index + 1 !== headers.length}
                  />
                </div>
            )}
          </div>
          <div
            ref={activeDropdown === column.headerName ? filterRef : null}
            style={{
              opacity: activeDropdown === column.headerName ? '1' : '0',
              pointerEvents: activeDropdown !== column.headerName && 'none',
            }}
          >
            <Filter
              ref={childRefs[index]}
              headerName={column.headerName}
              component={activeDropdown}
              filter={column.filter}
              onFilter={handleOnFilter}
              onHideForm={() => setActiveDropdown('')}
              index={index}
              headersLength={headers.length}
            />
          </div>
        </th>
      );
    }
    return null;
  };

  return (
    <tr
      className={styles.row}
      style={{ pointersEvent: isFetchingData && 'none', tableLayout: 'fixed' }}
    >
      {tableHeaders ? (
        <>{tableHeaders.map((column, index) => header(column, index))}</>
      ) : (
        <th className={styles.columns}>
          <div />
        </th>
      )}
      {!isLastColumnSmall && !isLastColumnSmallWithText && (
        <th className={styles.scrollWidthFiller}>
          <div />
        </th>
      )}
    </tr>
  );
}
TableHeader.propTypes = {
  data: PropTypes.arrayOf(PropTypes.shape),
  isFetchingData: PropTypes.bool,
  sortParameters: PropTypes.shape,
  queryParameters: PropTypes.arrayOf(PropTypes.shape),
  headers: PropTypes.arrayOf(PropTypes.shape).isRequired,
  onSort: PropTypes.func.isRequired,
  onFilter: PropTypes.func.isRequired,
  onClearFilters: PropTypes.func.isRequired,
};
TableHeader.defaultProps = {
  isFetchingData: true,
  sortParameters: null,
  queryParameters: null,
  data: [],
};
export default TableHeader;
