import PropTypes from 'prop-types';

import styles from './styles.module.css';

function BootstrapSpinner({ size }) {
    return (
        <div style={{ width: '100%', height: '100%', position: 'relative' }}>
            <div className={styles.bootstrapSpinner} style={{ width: size, height: size }}>
                <i
                    className="fas fa-circle-notch fa-spin"
                    style={{ fontSize: size, color: '#008662' }}
                />
            </div>
        </div>
    );
};

BootstrapSpinner.propTypes = {
    size: PropTypes.string,
};
  
BootstrapSpinner.defaultProps = {
    size: '40px',
};

export default BootstrapSpinner;