import React, { forwardRef, useImperativeHandle, useRef } from 'react';
import PropTypes from 'prop-types';
import Checkbox from '../../filters/check-box/Checkbox';
import Date from '../../filters/date/Date';
import NumberRange from '../../filters/number-range/NumberRange';
import SelectBox from '../../filters/select-box/SelectBox';
import StartsWith from '../../filters/starts-with/StartsWith';

const Filter = forwardRef(
  ({ filter, onFilter, onHideForm, index, headersLength }, ref) => {
    const startsWithRef = useRef();
    const selectBoxRef = useRef();
    const dateRef = useRef();
    const checkboxRef = useRef();
    const numberRangeRef = useRef();

    useImperativeHandle(ref, () => ({
      clearFilters() {
        switch (filter && filter.type) {
          case 1:
            startsWithRef.current.clearFilters();
            break;
          case 2:
            selectBoxRef.current.clearFilters();
            break;
          case 3:
            dateRef.current.clearFilters();
            break;
          case 4:
            checkboxRef.current.clearFilters();
            break;
          case 5:
            numberRangeRef.current.clearFilters();
            break;
          default:
            break;
        }
      },
    }));

    return (
      <>
        {filter && filter.type === 1 && (
          <StartsWith
            ref={startsWithRef}
            queryNames={filter.queryNames}
            onFilter={onFilter}
            onHideForm={onHideForm}
            customStyle={filter.style ? filter.style : {}}
          />
        )}

        {filter && filter.type === 2 && (
          <SelectBox
            ref={selectBoxRef}
            list={filter.list}
            queryNames={filter.queryNames}
            onFilter={onFilter}
            onHideForm={onHideForm}
          />
        )}

        {filter && filter.type === 3 && (
          <Date
            ref={dateRef}
            queryNames={filter.queryNames}
            onFilter={onFilter}
            onHideForm={onHideForm}
            index={index}
            headersLength={headersLength}
          />
        )}

        {filter && filter.type === 4 && (
          <Checkbox
            ref={checkboxRef}
            list={filter.list}
            queryNames={filter.queryNames}
            onFilter={onFilter}
            onHideForm={onHideForm}
          />
        )}

        {filter && filter.type === 5 && (
          <NumberRange
            ref={numberRangeRef}
            queryNames={filter.queryNames}
            onFilter={onFilter}
            onHideForm={onHideForm}
          />
        )}
      </>
    );
  }
);

Filter.propTypes = {
  filter: PropTypes.shape,
  onFilter: PropTypes.func,
  onHideForm: PropTypes.func.isRequired,
  index: PropTypes.number,
  headersLength: PropTypes.number,
};
Filter.defaultProps = {
  filter: null,
  onFilter: null,
  index: null,
  headersLength: 0,
};
Filter.displayName = 'Filter';
export default Filter;
