import React, {
  forwardRef,
  useImperativeHandle,
  useEffect,
  useState,
} from 'react';
import PropTypes from 'prop-types';
import Button from '../../button/Button';

import styles from './styles.module.css';

const Checkbox = forwardRef(
  ({
    list, queryNames, onFilter, onHideForm,
  }, ref) => {
    const [didMount, setDidMount] = useState(false);
    const [data, setData] = useState([]);
    const [selectedItems, setSelectedItems] = useState([]);
    const [selectAllIsClicked, setSelectAllIsClicked] = useState(true);
    const [isFilterDisabled, setIsFilterDisabled] = useState(true);

    const selectItem = (selectedData) => {
      if (!selectedItems.includes(selectedData)) {
        setSelectedItems([...selectedItems, selectedData]);
      } else {
        setSelectedItems(selectedItems.filter((item) => item !== selectedData));
      }
      setIsFilterDisabled(false);
    };

    const selectAllItems = () => {
      if (selectedItems.length !== data.length) {
        setSelectAllIsClicked(true);
        setSelectedItems([
          ...selectedItems,
          ...data.filter((item) => selectedItems.every((o) => o !== item)),
        ]);
        setIsFilterDisabled(false);
      } else {
        setSelectAllIsClicked(false);
        setSelectedItems([]);
      }
    };

    const handleOnFilter = () => {
      onFilter({ [queryNames[0]]: selectedItems }, queryNames);
      onHideForm('');

      setIsFilterDisabled(true);
    };

    const handleClearFilter = () => {
      onFilter({ [queryNames[0]]: undefined }, queryNames);
      onHideForm('');

      setSelectedItems([]);
      setSelectAllIsClicked(false);
      setIsFilterDisabled(true);
    };

    useImperativeHandle(ref, () => ({
      clearFilters() {
        setSelectedItems(list);
        setIsFilterDisabled(true);
      },
    }));

    useEffect(() => {
      if (list !== null) {
        setData(list);
        setSelectedItems(list);
      }
      setDidMount(true);
      // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    useEffect(() => {
      if (selectedItems.length === data.length) {
        setSelectAllIsClicked(true);
      } else {
        setSelectAllIsClicked(false);
      }
      // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [selectedItems]);

    return (
      <div className={styles.body}>
        {didMount && (
          <>
            <div
              className={styles.item}
              onClick={selectAllItems}
              onKeyDown={selectAllItems}
              role="presentation"
              style={{ marginBottom: '5px' }}
            >
              <div
                className={`${styles.checkbox} ${selectAllIsClicked
                  && styles.activeCheckbox}`}
              />
              <div className={styles.label}>Select All</div>
            </div>

            {data.map((item) => (
              <div
                key={item.id}
                className={styles.item}
                onClick={() => selectItem(item)}
                onKeyDown={() => selectItem(item)}
                role="presentation"
                style={{ marginBottom: '5px' }}
              >
                <div
                  className={`${styles.checkbox} ${selectedItems.includes(
                    item,
                  ) && styles.activeCheckbox}`}
                />
                <div className={styles.label}>
                  {item.icon && (
                    <img className={styles.icon} src={item.icon} alt="icon" />
                  )}
                  {item.name}
                </div>
              </div>
            ))}

            <hr style={{ backgroundColor: '#6d6d73' }} />

            <div className={styles.footer}>
              <Button text="Clear" onClick={handleClearFilter} defaultBtn />
              <Button
                defaultBtn={false}
                text="Filter"
                onClick={handleOnFilter}
                disabled={isFilterDisabled || selectedItems.length === 0}
              />
            </div>
          </>
        )}
      </div>
    );
  },
);
Checkbox.propTypes = {
  list: PropTypes.arrayOf(PropTypes.shape),
  queryNames: PropTypes.arrayOf(PropTypes.string),
  onFilter: PropTypes.func.isRequired,
  onHideForm: PropTypes.func.isRequired,
};
Checkbox.defaultProps = {
  list: null,
  queryNames: null,
};
Checkbox.displayName = 'Checkbox';
export default Checkbox;
